import React, { Component } from 'react';
import './App.css';
import MusicEditor from './pages/MusicEditor';
import MusicExam from './pages/MusicExam';
import NavHeader from './pages/Header';
import MusicSetting from './pages/MusicSetting';
// import MusicExercise from './pages/MusicExercise';
import Home from './pages/Home';
import { Switch, Route } from 'react-router-dom'

//document.body.style.overflow="scroll";//显示页面水平和垂直滚动条 
document.body.style.overflow="hidden";//隐藏页面水平和垂直滚动条  
window.isMobile = /(iPhone|iPad|iPod|iOS|Android)/i.test(navigator.userAgent)

class App extends Component {
	render() {
		return (
			<div className="App">
				<NavHeader />
				<Main />
			</div>
		);
	}
}


const Main = () => (
	<div style={{display:"table"}}>
    <Switch>
      <Route exact path='/' component={Home}/>
      <Route path='/editor' component={MusicEditor}/>
      <Route exact path='/exam' component={MusicExam}/>
      <Route path='/exam/:type' render={(props) => ( <MusicExam config={{type: props.match.params.type, level: window.level}}/> )}/>
      <Route path='/exercise/:type/:qtype' render={(props) => ( <MusicSetting type={props.match.params.type} qtype={props.match.params.qtype}/> )}/>
    </Switch>
  </div>
)


export default App;



