
const textOfStep=["C","D","E","F","G","A","B"]

class QNoteElem {
	constructor() {
		this.step = 0 //0...6
		this.octave = 4
		this.alter = 0
		this.readonly = false

		//计算出的
		this.line = 0
		this.value = 0
	}

	setNoteValue() {
		//note value
		this.value = QNoteElem.valueFromStep(this.step, this.alter, this.octave)
	}
	add(deltaStep, yinshu) {
		var res = new QNoteElem() //{alter: 0}
		res.octave = this.octave
		res.step = this.step+deltaStep
		if (res.step>6) {
			res.step = res.step%7
			res.octave += 1
		}
		res.setNoteValue()
		// console.log(res)
		var newValue = this.value+yinshu
		res.alter = newValue-res.value
		res.value = newValue
		return res
	}
	
	//根据clef, note.step, note.octave, note.alter计算note.accidental
	setNoteValueLineAcc(clef = "treble") {
		this.setNoteValue()
		//note line
		var line = this.step
		if (clef === "treble") {
			line -= 6
			line += (this.octave-4)*7
		}else{
			line -= 1
			line += (this.octave-3)*7
		}
		this.line = line

		//note accidental
		if (this.alter === 1) {
			this.accidental = "sharp"
		}else if (this.alter === 2) {
			this.accidental = "sharp-sharp"
		}else if (this.alter === -1) {
			this.accidental = "flat"
		}else if (this.alter === -2) {
			this.accidental = "flat-flat"
		}
	}
	textOfStep() {
		return textOfStep[this.step]
	}
	textOfAlter() {
		if (this.alter === 1) {
			return "#"
		}else if (this.alter === 2) {
			return "x"
		}else if (this.alter === -1) {
			return "b"
		}else if (this.alter === -2) {
			return "bb"
		}
		return ""
	}
	static valueFromStep(step, alter, octave) {
		var val = octave*12
		if (step<3) {
			val += 2*step
		}else{
			//step:3 -> value: 5
			//4 -> 7, 5->9 6->11
			val += 3+2*(step-2)
		}

		return val+(alter ? alter : 0)
	}
	static noteFromLine(line, alter, clef) {
		let yinhu
		if (clef === "bass") {
			//-6 C: 60, -5 D: 62, -4 E: 64, -3 F: 65, -2 G: 67, -1 A: 69, 0 B: 71, 1 C: 72
			yinhu = line+1+7*3
		}else{
			//-1 C, 0 D, 1 E, 1 F
			yinhu = line+6+7*4
		}
		let note = new QNoteElem()
		note.octave = Math.floor(yinhu/7)
		note.step = yinhu%7
		note.alter = alter
		note.setNoteValueLineAcc(clef)
		return note
	}
	// static valueFromLine(line, alter, clef) {
	// 	//-6 C: 60, -5 D: 62, -4 E: 64, -3 F: 65, -2 G: 67, -1 A: 69, 0 B: 71, 1 C: 72
	// 	var yinhu = line+6+7*4
	// 	var octave = Math.floor(yinhu/7)
	// 	var step = yinhu%7
	// 	console.log("octave:"+octave+" step:"+step)
	// 	return this.valueFromStep(step, alter, octave)
	// }
	//生成min...max之间的随机整数
	static random(max, min=0) {
		return Math.floor((max-min+1)*Math.random()+min)
	}

	//生成随机note {step: Int, octave: 4, alter: -2...2}
	static randomNote(alter, clef="treble") {
		var note = new QNoteElem()
		note.step = this.random(6) //Math.floor(7*Math.random()) //0...6
		note.alter = alter===0 || alter ? alter : this.random(1, -1) //Math.floor(3*Math.random()-1) //-1...1
		note.octave = 4
		note.setNoteValueLineAcc(clef)
		return note
	}
}
export default QNoteElem;



