import React from 'react';
import MusicScore from "../music/MusicScore";
import Staff from "../components/MusicStaff";
import {lineh} from '../config/MusicConfig';
import Toolbar, {iconfont, color} from "../components/Toolbar";
import Questions from "../questions/Questions";
import ListView from "../util/ListView";

//showListView
const [ListNone, ListLevels] = [0,1]
const ChooseChars = "ABCDEFG"
class MusicExam extends React.Component {
	constructor(props) {
		super(props);
		this.config = this.props.config ? this.props.config : {}
		this.state = {
			addnotes: [],
			curNoteIndex: 0,
			curElemIndex: 0,

			// questionTypes: types,
			questions: Questions.genQuestions(this.config, 1),
			questionIndex: 0, //-1 没有开始，0...n 做题中， 
			answer: -1,
			answeredAll:0,
			answeredWrongs: 0,
			showListView: ListNone,
		};
	}
	showQuestions(offset) {
		var questionIndex = this.state.questionIndex
		var questions = this.state.questions
		if (offset && questions.length>0) {
			var index = questionIndex+offset
			if (index>=0 && index<questions.length) {
				questionIndex = index
			}else if (index>=questions.length){
				let newQuestions = Questions.genQuestions(this.config, 1)
				questions.push(...newQuestions)
				questionIndex = questions.length-1
			}else{
				return
			}
		}else {
			// console.log(this.props.match.params)
			// let types = this.state.questionTypes
	        questions = Questions.genQuestions(this.config, this.config.num ? this.config.num : 1)
	        questionIndex = 0
	        if (!questions || questions.length===0) {
	        	console.log("error: genQuestions empty for type:"+this.config.type)
	        }
		}

	    return {
	    	addnotes: [],
	    	curNoteIndex: 0,
	    	curElemIndex: 0,

	    	questions: questions,
	    	questionIndex: questionIndex,
	    	answer: -1,
	    }
	}
	timeout(ms) {
		return new Promise((resolve, reject) => {
			setTimeout(resolve, ms, 'done');
		});
	}
	checkAnswer() {
		let answeredAll = this.state.answeredAll+1
		let answeredWrongs = this.state.answeredWrongs
		var question = this.state.questions[this.state.questionIndex]
		if (question && question.checkAnswer(this.state.answer, this.state.addnotes)) {
			this.timeout(1000).then((value) => {
				//console.log(value);
				let newState = this.showQuestions(1)
				this.setState(prevState => (newState))
			});
			question.correct = true
			return {answeredAll: answeredAll}
		}else if (question) {
			question.correct = false
			answeredWrongs += 1
		}
		
		return {answeredAll: answeredAll, answeredWrongs: answeredWrongs}
	}
	messageOfAnswer(question) {
		if (question.correct === undefined) {
			return
		}
		if (question.correct) {
			return "回答正确"
		}else{
			let msg = "回答错误"
			if (!question.answer.notes) {
				let answer = ChooseChars.charAt(question.answer)
				msg += ` 正确答案是：${answer}`
			}
			return msg
		}
	}
	accidentalOfAlter(alter) {
		if (alter === 1) {
			return "sharp"
		}else if (alter===2) {
			return "sharp-sharp"
		}else if (alter===-1) {
			return "flat"
		}else if (alter===-2) {
			return "flat-flat"
		}
	}
	mergeNotes(notes, addnotes) {
		if (addnotes.length===0) {
			return notes
		}
		let res = []
		let max = Math.max(notes.length, addnotes.length)
		for (var i = 0; i < max; i++) {
			let note = notes[i]
			let addnote = addnotes[i]
			let newNote = {note: 4}
			if (note && addnote) {
				newNote.elems = note.elems.concat(addnote.elems)
			}else if (note) {
				newNote.elems = note.elems
			}else {
				newNote.elems = addnote.elems
			}
			res.push(newNote)
		}
		return res
	}
	showQuestion() {
		var question = this.state.questions[this.state.questionIndex]
		if (!question) {
			console.log("error: questions is empty")
			console.log(this.state.questions)
			return
		}
		var items = []
		var title = (this.state.questionIndex+1)+". "+question.desc
	    items.push(<div key={0}>{title}</div>)

        if (question.score) {
        	let score
        	let fifths = question.score.fifths ? question.score.fifths : 0
        	let clef = question.score.clef ? question.score.clef : "treble"
        	if (question.score.notes) {
        		let notes = this.mergeNotes(question.score.notes, this.state.addnotes)
		        score = MusicScore.createScore(notes, clef, fifths)

		        //显示参考答案
		        // if (question.correct === false && question.answer.notes) {
	        	// 	let notes = this.mergeNotes(question.score.notes, question.answer.notes)
		        // 	//let notes = question.score.notes.concat(question.answer.notes)
		        // 	let correctElems = []
		        // 	for (let note of notes) {
		        // 		correctElems.push({line: note.line, accidental: note.accidental})
		        // 	}	
		        // 	let correctNote = {note: 8, elems:correctElems}
		        // 	score.parts[0].measures[0].staves[0].push(correctNote)
		        // }
		        for(let n of notes) {
		        	if (!n) {
		        		console.log("error")
		        	}
		        }
        	}else{
        		score = MusicScore.createScore(this.state.addnotes, clef, fifths)
        	}

	        var cursor
	        if (this.state.addnotes.length>0) {
	        	let curElemIndex =this.state.curElemIndex
	        	if (question.score.notes) {
		        	let refNote = question.score.notes[this.state.curNoteIndex]
					if (refNote) {
						curElemIndex += refNote.elems.length
					}
	        	}
	        	cursor = {partIndex: 0, measureIndex: 0, staffIndex:0, noteIndex: this.state.curNoteIndex, elemIndex: curElemIndex}
	        }
	        items.push(<Staff key={items.length} score={score} cursor={cursor} top={60} height={160}/>)
        }
        if (question.choices) {
        	for (let k in question.choices) {
        	// for (var k = 0; k < question.choices.length; k++) {
        		var s = question.choices[k]
        		var subtitle = ChooseChars.charAt(k) + ". " + s + "  "
			    items.push(<div key={items.length}>{subtitle}</div>)
        	}
        }
    	if (question && question.correct !== undefined) {
    		// var question = this.state.questions[this.state.questionIndex]
    		let msg = this.messageOfAnswer(question)
    		items.push(<div key={items.length} style={{color: question.correct ? "blue" : "red"}}>{msg}</div>)
    	}
        return (<div>{items}</div>)
	}
	get curNote() {
		let index = this.state.curNoteIndex
		if (index>=this.state.addnotes.length) {
			index = this.state.addnotes.length-1
		}
		return this.state.addnotes[index]
	}
	takeAction(action) {
		let newState
		switch (action.type) {
		case 'showlevel':
			newState = {showListView: ListLevels}
			break
		case 'selectLevel':
			window.level = parseInt(action.value, 10)
			newState = this.showQuestions(0)
			newState.showListView = ListNone
			break
		case 'questions':
			newState = this.showQuestions(action.offset)
			break
		case 'answer':
			newState = {answer: action.value}
			break
		case 'checkAnswer':
			newState = this.checkAnswer()
			break
		case 'addNoteElem':
			if (this.state.curNoteIndex>=this.state.addnotes.length) {
				this.state.addnotes.push({note: 4, elems:[]})
			}
			let curNoteElems = this.curNote.elems
			curNoteElems.push({line: 0})
			let curElemIndex = curNoteElems.length-1
			
			newState = {
				addnotes: this.state.addnotes, 
				// curNoteIndex: curNoteIndex,
				curElemIndex: curElemIndex,
			}
			break
		case 'addNote':
			let curNoteIndex = this.state.curNoteIndex
			if (this.state.addnotes.length===0) {
				curNoteIndex = 0
			}else{
				curNoteIndex += 1
			}
			this.state.addnotes.splice(curNoteIndex, 0, {note: 4, elems:[{line: 0}]})
			// this.state.addnotes.push()

			this.state.curNoteIndex = curNoteIndex
			newState = {
				addnotes: this.state.addnotes, 
				curNoteIndex: curNoteIndex,
				curElemIndex: 0,
			}
			break
		case 'delNote':
			if (this.state.curNoteIndex>=this.state.addnotes.length) {
				return
			}
			this.curNote.elems.splice(this.state.curElemIndex, 1)
			if (this.curNote.elems.length===0) {
				this.state.addnotes.splice(this.state.curNoteIndex, 1)
				if (this.state.curNoteIndex>0) {
					this.state.curNoteIndex -= 1
				}
			}
			newState = {addnotes: this.state.addnotes, curNoteIndex: this.state.curNoteIndex, curElemIndex: this.state.addnotes.length-1}
			break
		case 'moveCursor':
			let noteIndex = this.state.curNoteIndex+action.offset
			if (noteIndex<0 || noteIndex>=this.state.addnotes.length) {
				return
			}
			newState={
				curNoteIndex: noteIndex
			}
			break
		case 'moveChordLevel':
			var chord = this.curNote.elems[this.state.curElemIndex]
			var line = chord.line+action.offset
			chord.line = line
			newState = {addnotes: this.state.addnotes}
			break
		case 'selectChord':
			var index = (this.state.curElemIndex+1)%this.curNote.elems.length
			newState = {curElemIndex: index}
			break
		case 'setNoteAlter':
			var note = this.curNote.elems[this.state.curElemIndex]
			if (action.offset === 1) {
				if (note.alter === 1) {
					note.alter = 2
				}else if (note.alter === 2) {
					note.alter = 0
				}else {
					note.alter = 1
				}
			}else{
				if (note.alter === -1) {
					note.alter = -2
				}else if (note.alter === -2) {
					note.alter = 0
				}else {
					note.alter = -1
				}
			}
			note.accidental = this.accidentalOfAlter(note.alter)
			newState = {addnotes: this.state.addnotes}
			break
		default:
		}
		this.setState(prevState => (newState))
	}
	get scoreToolbar() {
	    var buttons = []
		// const btnKeys = ["C", "D", "E", "F", "G", "A", "B", "C", "D", "E", "F", "G", "A", "B", "C", "D", "E", "F", "G", "A", "B", "C"]
		// for (var i = btnKeys.length - 1; i >= 0; i--) {
		// 	const key = 48+this.keyOfStep(i)
		// 	buttons.push({title:btnKeys[i], key: key, background:"rgba(0,0,0,0)", color:"#000", onClick:(e) => this.inputNoteKeys([key]) })
		// }

		buttons.push(
		// {title:"<", key: 50, action: {type: "questions", offset: -1}, background: color.pink},
		// {title:"试题", key: 51, action: {type: "showlevel"}, background: color.pink},
		// {title:">", key: 52, action: {type: "questions", offset: 1}, background: color.pink},

		// {title:"音程", key: 73, action: {type: "interval"}, background: color.pink},
		// {title:"和弦", key: 75, action: {type: "chord"}, background: color.pink},
		// {title:"和弦", key: 78, action: {type: "setchord"}, background: color.pink},
		// {title:"三", key: 58, action: {type: "chord", value: 3}, background: color.pink},
		// {title:"七", key: 61, action: {type: "chord", value: 7}, background: color.pink},
		// {title:"九", key: 63, action: {type: "chord", value: 9}, background: color.pink},
		// {title:"十一", key: 66, action: {type: "chord", value: 11}, background: color.pink},
		)
		var question = this.state.questions[this.state.questionIndex]
		if (question && question.choices) {
			for (var i = 0; i < question.choices.length; i++) {
        		var s = ChooseChars.charAt(i)
				buttons.push({title:s, key: 41+i, action: {type: "answer", value: i}, background: this.state.answer===i ? color.pink : color.blue})
        	}
        }else if (question && question.answer.notes) {
        	let key = 41
        	if (question.score.chordOnly) {
        		buttons.push({iconfont:iconfont.noteQuarter, key: key, action: {type: "addNoteElem"}, background: color.blue})
        		key += 1
        	}else{
	        	buttons.push({iconfont:iconfont.noteQuarter, key: key+1, action: {type: "addNote"}, background: color.blue})
	        	buttons.push({iconfont:iconfont.arrowLeft, key: key+2, action: {type: "moveCursor", offset: -1}, background: color.blue})
	        	buttons.push({iconfont:iconfont.arrowRight, key: key+3, action: {type: "moveCursor", offset: 1}, background: color.blue})
        		key += 3
        	}
        	if (this.state.addnotes.length>0 && this.curNote.elems.length>0) {
	        	buttons.push({iconfont:iconfont.arrowUp, key: key, action: {type: "moveChordLevel", offset: 1}, background: color.blue})
	        	if (this.curNote.elems.length>1) {
		        	buttons.push({iconfont:iconfont.arrowChordSwitch, key: key+1, action: {type: "selectChord"}, background: color.blue})
	        	}
	        	buttons.push({iconfont:iconfont.arrowDown, key: key+2, action: {type: "moveChordLevel", offset: -1}, background: color.blue})
	        	buttons.push({iconfont:iconfont.accSharp, key: key+3, action: {type: "setNoteAlter", offset:1}, background: color.blue})
	        	buttons.push({iconfont:iconfont.accFlat, key: key+4, action: {type: "setNoteAlter", offset:-1}, background: color.blue})

	        	buttons.push({iconfont:iconfont.delete, key: key+5, action: {type: "delNote"}, background: color.blue})
        	}
		}
        buttons.push({title:"OK", key: 51, action: {type: "checkAnswer"}, background: color.control})
		return <Toolbar items={buttons} action={(action) => this.takeAction(action)}/>
	}
	render() {
		var toolbar = this.scoreToolbar
		var score = this.showQuestion()
		var listview = null
		if (this.state.showListView === ListLevels) {
			let types = ["1级", "2级", "3级", "4级", "5级"]
			listview = <ListView list={types} bottom={lineh*2} action={(index, name) => this.takeAction({type: "selectLevel", value: name})}/>
		}
		// if (this.props.config.type) {
		// 	var types = " - " + this.props.config.type
		// 	//var types = " - " + this.props.config.types.join(",")
		// }
		let rights = this.state.answeredAll-this.state.answeredWrongs
		let percent = this.state.answeredAll===0 ? 0 : Math.floor(100*rights/this.state.answeredAll)
		let types = ` ${rights}/${this.state.answeredAll}  ${percent}%`

		return (
		<div>
			<div style={{position: "absolute", left: 10, top: 30, width: 700}}>
				<h3>Find音乐学院基本乐科{this.config.level ? `${this.config.level}级` : '' }{types}</h3>
				{score}
				{listview}
			</div>
			{toolbar}
		</div>
		)
	}
}
export default MusicExam;



