import QInterval from './QInterval';
import QChord from './QChord';
import QNoteElem from './QNote';
import QScale from './QScale';

/*
params:
	config: null为随机音程
	{
		steps: null 随机度数, 或[1,2,3,4,5,6,7,8] 度音程, 
		types: null 随机类型，或["倍减","减","小","纯","大","增","倍增"]
	}
return:
	{
		name: 音程名字
		description: 描述
		notes: [{step, octave, alter, line, value},...]
	}
*/

export function randomInterval(config) {
	// config = {steps: [1,2], types:["小","纯","大"]}
	let interval = QInterval.randomInterval(config)
	return interval
}

/*
params:
	config: null为随机和弦
		{
			types: null 随机和弦, 或[3、7、9、11、13] 和弦, 
			inversions: null 随机转位，或第[0，1，2，3]转位 

		}
return:
	{
		name: 和弦名字
		description: 描述
		notes: [{step, octave, alter, line, value},...]
	}
*/
export function randomChord(config) {
	// config = {types:[3,7], inversions:[1,2]}
	let chord = QChord.randomChord(config)
	return chord
}
