const noteNames=["C","D","E","F","G","A","B"]
class NoteQuestions {
	/*
	config={
		level: 1...5
		clef: treble or bass
	}
	*/
	static genQuestions(config) {
		let questions = []
		if (!config.qtypes ||  config.qtypes.includes('Identification')) {
			this.question0(questions, config)
		}
		if (!config.qtypes ||  config.qtypes.includes('Construction')) {
			this.question1(questions, config)
		}
		return questions
	}
	static question0(questions, config) {
		let note = QNoteElem.randomNote()
		let noteName = noteNames[note.step]
		var choices = ["A","B","C","D","E","F","G"]
		var answer = choices.indexOf(noteName)
		let score = {notes:[{note:1,elems:[note]}]}
		var question = new Question()
		question.desc = "选择音名"
		question.score = score
		question.choices = choices
		question.answer = answer
		
		questions.push(question)
	}
	static question1(questions, config) {
		let clef = (config && config.clefs.includes("低音")) ? "bass" : "treble"
		let note = QNoteElem.randomNote(null, clef)
		let noteName = noteNames[note.step]
		if (note.alter===1) {
			noteName += "#"
		}else if (note.alter === -1) {
			noteName += "b"
		}
		var question = new Question()
		question.desc = "写出"+noteName+"音"
		question.score = {fifths: 0, clef: clef}
		question.answer = {ignoreOctave: true, notes: [{note:1,elems:[note]}]}
		
		questions.push(question)
	}

}
class IntervalQuestions {
	/*
	音程（一级两个选项，二级二三度，三级如大小六度）
		a. 选择音程（给定一个音程的五线谱，选择音程名称）
	1. 八度以内的自然音程（纯白键）
	2. 八度以内的自然音程（带升，降，还原）
	3. 十五度以内的自然音程（带升，降，还原）
	4. 八度以内的变化音程（包含重升，重降记号）倍增和倍减
	5. 十五度以内的变化音程（带重升，重降）倍增和倍减
	config={
		level: 1...5
		types: ["减","小","纯","大","增"]
		qtypes: ['判断性质','指定低、冠音写作']
		steps: [3,7,9,11,13]
	}
	*/
	static genQuestions(config) {
		let questions = []
		if (config.level) {
			config = {}
			if (config.level<3 || config.level===4) {
				config.steps = [1,2,3,4,5,6,7,8]
			}
			if (config.level===1) {
				config.alters = [0]
			}else if (config.level===2 || config.level===3) {
				config.alters = [-1, 0, 1]
			}

			if (config.level < 4) {
				config.types = ["减", "小","纯","大", "增"]
			}
		}
		if (!config.qtypes ||  config.qtypes.includes('Identification')) {
			this.intervalQuestion0(questions, config)
		}
		if (!config.qtypes ||  config.qtypes.includes('Construction')) {
			this.intervalQuestion1(questions, config)
			this.intervalQuestion2(questions, config)
		}
		return questions
	}
	static intervalQuestion0(questions, config) {
		var interval = QInterval.randomInterval(config)
		if (!interval.name) {
			console.log('error: to generate random interval')
			console.log(interval)
		}
		var choices = QInterval.randomIntervalNames(config, 3, interval.name)
		var answer = QNoteElem.random(choices.length)
		choices.splice(answer, 0, interval.name)

		let score = {notes:[{note:8,elems:[interval.root, interval.head]}]}
		var question = new Question()
		question.desc = "选择音程的名称"
		question.score = score
		question.choices = choices
		question.answer = answer
		
		questions.push(question)
	}
	static intervalQuestion1(questions, config) {
		var interval = QInterval.randomInterval(config)
		var question = new Question()
		question.desc = "以指定音为低音,构成指定的音程："+interval.name
		question.score = {chordOnly: true, notes: [{note:8,elems:[interval.root]}]}
		question.answer = {notes: [{note:8,elems:[interval.head]}]}
		
		questions.push(question)
	}
	static intervalQuestion2(questions, config) {
		var interval = QInterval.randomInterval(config)
		var question = new Question()
		question.desc = "以指定音为冠音,构成指定的音程："+interval.name
		question.score = {chordOnly: true, notes:[{note:8, elems:[interval.head]}]}
		question.answer = {notes: [{note:8, elems: [interval.root]}]}
		questions.push(question)
	}
}
class ChordQuestions {
	/*
	1. 原位和转位的三和弦（纯白键）	
	2. 原位和转位的七和弦（纯白键）	
	3. 原位与转位的三和弦（带升，降，还原）	
	4. 原位与转位的七和弦（带重升，重降）	
	5. 九，十一，十三和弦	
	备注：目前国内与乐理考试最终只测验7和弦	
乐理考试中不会出现属和弦字样，所以在七和弦中，不出现属七和弦与增属和弦两个品种	
乐理考试中的属七和弦会被命名为大小七和弦	
	config={
		level: 1...5
		types: ["大三和弦", "小三和弦", "减三和弦", "增三和弦", "大七和弦", "小七和弦", "大小七和弦", "减七和弦", "半减七和弦", "增大七和弦", "小大七和弦"]
		qtypes: [QTChooseQuality,QTWriteWithRootThirdFifth, QTWriteWithBottom]
		//steps: [3,7,9,11,13]
		inversions: ['转位']
	}
	*/
	static genQuestions(config) {
		let questions = []
		if (config.level) {
			config = {}
			if (config.level === 1) {
				config.steps = [3]
				config.alters = [0]
			}else if (config.level === 2) {
				config.steps = [7]
				config.alters = [0]
			}else if (config.level === 3) {
				config.steps = [3]
				config.alters = [-1, 0, 1]
			}else if (config.level === 4) {
				config.steps = [7]
				// config.alters = [-2, -1, 0, 1, 2]
			}else {
				config.steps = [9, 11, 13]
			}
		}
		if (config.inversions && config.inversions.length>0) {
			config.inversions = [0,1,2]
		}else{
			config.inversions = [0]
		}
		if (!config.qtypes ||  config.qtypes.includes('Identification')) {
			this.chordQuestion1(questions, config)
		}
		if (!config.qtypes ||  config.qtypes.includes('Construction')) {
			this.chordQuestion2(questions, config)
			if (!config.qtypes || config.inversions) {
				this.chordQuestion3(questions, config)
			}
		}
		return questions
	}
	static copyNotes(notes, expectIndex) {
		// var answers = [...notes]
		// console.log("copyNotes:")
		// console.log(answers)
		// answers.splice(expectIndex, 1)
		// console.log(answers)
		var elems = []
		for (var k = 0; k < notes[0].elems.length; k++) {
			if (k !== expectIndex) {
				elems.push(notes[0].elems[k])
			}
		}
		// console.log(answers)
		return [{note:notes[0].note, elems: elems}]
	}
	static chordQuestion1(questions, config) {
		var chord = QChord.randomChord(config)
		if (!chord.name) {
			console.log("error: QChord.randomChord")
			console.log(chord)
		}
		var choices = QChord.randomChordNames(config, 3, chord.name)
		var answer = QNoteElem.random(choices.length)
		choices.splice(answer, 0, chord.name)

		var question = new Question()
		question.desc = "选择和弦的名称"
		question.score = {notes:chord.notes}
		question.choices = choices
		question.answer = answer
		
		questions.push(question)
	}
	static chordQuestion2(questions, config) {
		var types = ["根音", "三音","五音"]
		var inversions = config.inversions
		config.inversions = [0]
		for (var i = 0; i < types.length; i++) {
			let chord = QChord.randomChord(config)
			if (chord) {
				let question = new Question()
				question.desc = "以指定的音为"+types[i]+"，构成和弦："+chord.name
				question.score = {chordOnly: true, notes:[{note:8, elems:[chord.notes[0].elems[i]]}]}
				question.answer = {notes: this.copyNotes(chord.notes, i)} //{notes: chord.notes}
				questions.push(question)
			}
		}
		config.inversions=inversions
	}
	static chordQuestion3(questions, config) {
		var inversions = config.inversions
		config.inversions=[1,2]
		let chord = QChord.randomChord(config)
		if (chord) {
			let question = new Question()
			question.desc = "以指定的音为低音，构成和弦："+chord.name
			question.score = {chordOnly: true, notes:[{note:8, elems:[chord.notes[0].elems[0]]}]}
			question.answer = {notes: this.copyNotes(chord.notes, 0)} //{notes: chord.notes}
			questions.push(question)
		}
		config.inversions=inversions
	}
	
}

class KeyQuestions {
	/*config={
		level: 1...5
		keySignatures: [-7 ... 7]
	}
	*/
	static genQuestions(config) {
		let questions = []
		this.genQuestion1(questions, config)
		return questions
	}
	static genQuestion1(questions, config) {
		let scale = QScale.randomScale(config.keySignatures)
		var choices = QScale.randomKeyNames(config, 5, scale.fifths)
		// if (choices[0]==="undefined大调") {
		// 	console.log("error to randomKeyNames, fifths:"+scale.fifths)
		// 	console.log(scale)
		// }
		var answer = QNoteElem.random(choices.length)
		choices.splice(answer, 0, scale.name)

		var question = new Question()
		question.desc = "选择大调的名称"
		question.score = {fifths: scale.fifths}
		question.choices = choices
		question.answer = answer
		
		questions.push(question)
	}
}

class ScaleQuestions {
	/*config={
		level: 1...5
		keySignatures: [-7 ... 7]
		scales: ["自然大调", "自然小调","和声小调","旋律小调"]
		qtypes: [QTWriteScale, QTChooseScaleType]
	}
	*/
	static genQuestions(config) {
		let questions = []
		if (!config.qtypes ||  config.qtypes.includes('Identification')) {
			this.genQuestion1(questions, config)
		}
		if (!config.qtypes ||  config.qtypes.includes('Construction')) {
			this.genQuestion2(questions, config)
		}
		return questions
	}
	static genQuestion1(questions, config) {
		let scale = QScale.randomScale(config.keySignatures, config.scales)
		var choices = ["自然大调","自然小调","和声小调","旋律小调"]
		// var answer = QNoteElem.random(choices.length)
		// choices.splice(answer, 0, scale.name)
		let answer = choices.indexOf(scale.type)
		if (answer<0) {
			console.log("error: to set answer:"+scale.type)
			console.log(choices)
		}

		var question = new Question()
		question.desc = "选择音阶调式"
		question.score = {notes: scale.notes}
		question.choices = choices
		question.answer = answer
		
		questions.push(question)
	}
	static genQuestion2(questions, config) {
		let scale = QScale.randomScale(config.keySignatures, config.scales)
		if (!scale.name) {
			console.log("error: randomScale ")
			console.log(scale)
		}
		// var choices = ["自然大调","自然小调","和声小调","旋律小调"]
		// var answer = QNoteElem.random(choices.length)
		// choices.splice(answer, 0, scale.name)
		// let answer = 0
		// let notes = []
		// for (var i = 0; i < 7; i++) {
		// 	notes.push({note:1, elems:[{line:0, readonly: true}]})
		// }
		var question = new Question()
		question.desc = "写出"+scale.name+"音阶"
		question.score = {fifths: 0}
		// question.choices = choices
		question.answer = {notes: scale.notes}
		
		questions.push(question)
	}
}

class Questions {
	static genQuestionsTo(questions, Class, config) {
		let q = Class.genQuestions(config)
		if (q) {
			questions.push(...q)
		}
	}
	/*
	config: {
		type: "interval" or "chord" or "scale"
		level: null 随机难度， 1-5级难度
	}
	return:[Question]
	question:{
		desc: "",
		score: {notes: []},
		choices: [string]
		answer: {notes: []} or index of choices
	}
	*/
	static genQuestions(config, num=1) {
		var questions = []
		for (var i = 0; i < num; i++) {
			if (!config.type || config.type==="note") {
				this.genQuestionsTo(questions, NoteQuestions, config)
			}
			if (!config.type || config.type==="interval") {
				this.genQuestionsTo(questions, IntervalQuestions, config)
			}
			if (!config.type || config.type==="chord") {
				this.genQuestionsTo(questions, ChordQuestions, config)
			}
			if (!config.type || config.type==="keysignature") {
				this.genQuestionsTo(questions, KeyQuestions, config)
			}
			if (!config.type || config.type==="scale") {
				this.genQuestionsTo(questions, ScaleQuestions, config)
			}
		}
		return questions
	}
}

export default Questions;






